<template>
  <div class="available-nfts">
    <h2 class="section-title">Available NFTs for Rent</h2>
    
    <div class="filters" v-if="false">
      <input 
        type="text" 
        v-model="searchQuery" 
        placeholder="Search by name..."
        class="search-input"
      />
      <div class="price-filter">
        <span>Max daily price: {{ maxPriceFilter }} SOL</span>
        <input 
          type="range" 
          v-model="maxPriceFilter" 
          min="0" 
          max="10" 
          step="0.1"
          class="price-slider"
        />
      </div>
    </div>
    
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading available NFTs...</p>
    </div>
    
    <div v-else-if="filteredNfts.length === 0" class="empty-state">
      <p>No NFTs available for rent right now.</p>
      <button @click="refreshData" class="refresh-btn">Refresh</button>
    </div>
    
    <div v-else class="nft-grid">
      <div v-for="nft in filteredNfts" :key="nft.id" class="nft-card">
        <div class="nft-image-container">
          <img :src="nft.imageUrl || '/images/default-nft.png'" :alt="nft.name" class="nft-image" />
        </div>
        <div class="nft-info">
          <h3 class="nft-name"><router-link :to="`/nft/${nft.nftMint}`">{{ nft.name || 'Unnamed NFT' }}</router-link></h3>
          <p class="nft-owner">Owner: <router-link :to="`/player/${nft.userTag}`">{{ formatUserTag(nft.userTag) }}</router-link></p>
          <div class="nft-price">
            <span class="price-label">Daily Price:</span>
            <span class="price-amount">{{ formatPrice(nft.dayPrice) }} SOL</span>
          </div>
          <div class="nft-details">
            <div class="detail-item">
              <span class="detail-label">Max rental duration:</span>
              <span class="detail-value">{{ nft.maxDays }} day(s)</span>
            </div>
          </div>
          
          <div v-if="isAuthenticated && walletAddress" class="rent-controls">
            <div v-if="nft.userTag === userTag" class="own-nft-message">
              <div class="own-nft-label">Your Listed NFT</div>
              <p class="own-nft-description">This NFT is listed by you for rent</p>
            </div>
            
            <div v-if="nft.userTag !== userTag" class="rent-options">
              <div class="rental-period">
                <label for="rental-days" class="rental-period-label">Rental period:</label>
                <div class="custom-select" @click="toggleDropdown(nft.id)" :class="{ 'is-open': openDropdown === nft.id }">
                  <div class="selected-option">
                    {{ rentDays[nft.id] ? `${rentDays[nft.id]} day${rentDays[nft.id] > 1 ? 's' : ''} ${getDiscountText(rentDays[nft.id])}` : 'Select period' }}
                  </div>
                  <div class="options-dropdown" v-if="openDropdown === nft.id">
                    <div 
                      v-for="i in getRentDaysOptions(nft.maxDays)" 
                      :key="i" 
                      class="option"
                      :class="{ 'selected': rentDays[nft.id] === i }"
                      @click.stop="selectDays(nft, i)"
                    >
                      {{ i }} day{{ i > 1 ? 's' : '' }} {{ getDiscountText(i) }}
                    </div>
                  </div>
                </div>
              </div>
              
              <button 
                @click="rentNft(nft)" 
                class="rent-button" 
                :disabled="!rentDays[nft.id]"
              >
                Rent NFT
              </button>
            </div>
          </div>
          
          <div v-else class="auth-message">
            <p>Please connect your wallet to rent NFTs</p>
          </div>
          
          <div v-if="rentDays[nft.id] && discountInfo[nft.id] && nft.userTag !== userTag" class="discount-info">
            <div class="discount-row">
              <span class="discount-label">Base price:</span>
              <span class="discount-value">{{ formatPrice(nft.dayPrice) }} SOL/day</span>
            </div>
            <div v-if="discountInfo[nft.id].discountPercent > 0" class="discount-row discount">
              <span class="discount-label">Discount:</span>
              <span class="discount-value">-{{ discountInfo[nft.id].discountPercent }}% OFF</span>
            </div>
            <div class="discount-row total">
              <span class="discount-label">Total for {{ rentDays[nft.id] }} days:</span>
              <span class="discount-value">{{ formatPrice(discountInfo[nft.id].totalPrice) }} SOL</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Rent Confirmation Modal -->
    <div v-if="showRentModal" class="modal-overlay" @click="closeRentModal">
      <div class="modal-content" @click.stop>
        <!-- Initial confirmation view -->
        <div v-if="!transactionStatus">
          <h3>Confirm NFT Rental</h3>
          <div v-if="selectedNft && currentRentalInfo" class="rental-summary">
            <div class="summary-item">
              <span class="summary-label">NFT:</span>
              <span class="summary-value">{{ selectedNft.name }}</span>
            </div>
            <div class="summary-item">
              <span class="summary-label">Rental Period:</span>
              <span class="summary-value">{{ rentDays[selectedNft.id] }} days</span>
            </div>
            <div class="summary-item">
              <span class="summary-label">Original Daily Price:</span>
              <span class="summary-value">{{ formatPrice(selectedNft.dayPrice) }} SOL</span>
            </div>
            
            <div v-if="currentRentalInfo.discountPercent > 0" class="summary-item discount">
              <span class="summary-label">Discount:</span>
              <span class="summary-value">{{ currentRentalInfo.discountPercent }}% OFF</span>
            </div>
            
            <div v-if="currentRentalInfo.discountPercent > 0" class="summary-item">
              <span class="summary-label">Discounted Daily Price:</span>
              <span class="summary-value">{{ formatPrice(currentRentalInfo.discountedDailyPrice) }} SOL</span>
            </div>
            
            <div class="summary-item total">
              <span class="summary-label">Total Cost:</span>
              <span class="summary-value">{{ formatPrice(currentRentalInfo.subtotal) }} SOL</span>
            </div>
            
            <div class="summary-item end-date">
              <span class="summary-label">Rental End Date:</span>
              <span class="summary-value">{{ formatDate(currentRentalInfo.rentalEndTime) }}</span>
            </div>
          </div>
          
          <div v-else-if="selectedNft" class="loading-info">
            <div class="loading-spinner small"></div>
            <p>Loading rental details...</p>
          </div>
          
          <div class="modal-actions">
            <button @click="closeRentModal" class="cancel-button">Cancel</button>
            <button 
              @click="confirmRent" 
              :disabled="rentLoading"
              class="confirm-button"
            >
              <span v-if="rentLoading" class="loading-spinner small"></span>
              <span v-else>Confirm Rental</span>
            </button>
          </div>
        </div>
        
        <!-- Transaction processing view -->
        <div v-else-if="transactionStatus === 'pending'">
          <h3 class="modal-title pending-title">Processing Transaction</h3>
          <div class="transaction-processing">
            <div class="loading-spinner pending-spinner"></div>
            <p class="pending-message">Your rental transaction is being processed...</p>
            <div class="transaction-info">
              <p class="small-text">Please wait while we confirm your transaction on the Solana blockchain.</p>
              <div class="pending-details">
                <div class="pending-detail">
                  <span class="detail-icon">💰</span>
                  <span>{{ formatPrice(currentRentalInfo.subtotal) }} SOL</span>
                </div>
                <div class="pending-detail">
                  <span class="detail-icon">⏱️</span>
                  <span>{{ rentDays[selectedNft.id] }} days rental</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        
        <!-- Success view -->
        <div v-else-if="transactionStatus === 'success'">
          <h3 class="modal-title success-title">Rental Successful!</h3>
          <div class="transaction-success">
            <div class="success-icon-container">
              <div class="success-icon">✓</div>
            </div>
            <div class="rental-summary success-summary">
              <div class="summary-item">
                <span class="summary-label">NFT:</span>
                <span class="summary-value">{{ selectedNft.name }}</span>
              </div>
              <div class="summary-item">
                <span class="summary-label">Rental Period:</span>
                <span class="summary-value">{{ currentRentalInfo.days }} days</span>
              </div>
              <div class="summary-item">
                <span class="summary-label">Total Cost:</span>
                <span class="summary-value">{{ formatPrice(currentRentalInfo.subtotal) }} SOL</span>
              </div>
              <div class="summary-item">
                <span class="summary-label">Rental End Date:</span>
                <span class="summary-value">{{ formatDate(currentRentalInfo.rentalEndTime) }}</span>
              </div>
              <div class="summary-item">
                <span class="summary-label">Transaction:</span>
                <a 
                  :href="`https://explorer.solana.com/tx/${transactionSignature}`" 
                  target="_blank" 
                  class="transaction-link"
                >
                  View on Solana Explorer
                </a>
              </div>
            </div>
            <p class="success-message">Your rented NFT is now available in the "My Rented NFTs" tab.</p>
          </div>
          <div class="modal-actions success-actions">
            <button @click="closeRentModal" class="cancel-button">Close</button>
            <router-link :to="`/nft/${selectedNft.nftMint}`" class="view-nft-button">
              Open NFT Page
            </router-link>
          </div>
        </div>
        
        <!-- Error view -->
        <div v-else-if="transactionStatus === 'error'">
          <h3 class="modal-title error-title">Rental Failed</h3>
          <div class="transaction-error">
            <div class="error-icon-container">
              <div class="error-icon">✗</div>
            </div>
            <div class="error-content">
              <p class="error-message">{{ transactionError || 'There was an error processing your rental transaction.' }}</p>
              <div class="error-details">
                <p class="small-text">Please try again or contact support if the problem persists.</p>
              </div>
            </div>
          </div>
          <div class="modal-actions error-actions">
            <button @click="closeRentModal" class="cancel-button">Close</button>
            <button @click="retryRental" class="retry-button">Try Again</button>
          </div>
        </div>
      </div>
    </div>
    
    <AppToast 
      :show="toast.show" 
      :message="toast.message" 
      :type="toast.type" 
      @close="toast.show = false" 
      :duration="toast.duration"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted, reactive, watch } from 'vue';
import store from '@/vuex';
import axios from 'axios';
import { PublicKey, Transaction } from '@solana/web3.js';
import AppToast from '@/components/Toast.vue';

export default {
  name: 'AvailableNfts',
  components: {
    AppToast
  },
  props: {},
  setup() {
    const loading = ref(true);
    const rentLoading = ref(false);
    const nfts = ref([]);
    const searchQuery = ref('');
    const maxPriceFilter = ref(10);
    const rentDays = reactive({});
    const discountInfo = reactive({});
    const showRentModal = ref(false);
    const selectedNft = ref(null);
    const currentRentalInfo = ref(null);
    const openDropdown = ref(null);
    const transactionStatus = ref(null); // 'pending', 'success', 'error'
    const transactionSignature = ref(null);
    const transactionError = ref(null);
    
    // Toast notification state
    const toast = reactive({
      show: false,
      message: '',
      type: 'info', // 'success', 'error', 'info', 'warning'
      duration: 5000
    });
    
    const isAuthenticated = computed(() => store.getters.isAuthenticated);
    const walletAddress = computed(() => store.getters.walletAddress);
    const userTag = computed(() => store.getters.userTag);
    
    const filteredNfts = computed(() => {
      return nfts.value
        .filter(nft => {
          // Apply search filter
          const nameMatch = nft.name && nft.name.toLowerCase().includes(searchQuery.value.toLowerCase());
          // Apply price filter
          const priceMatch = parseFloat(nft.dayPrice) <= maxPriceFilter.value;
          return nameMatch && priceMatch;
        })
        .sort((a, b) => parseFloat(a.dayPrice) - parseFloat(b.dayPrice));
    });
    
    const fetchAvailableNfts = async () => {
      try {
        loading.value = true;
        const response = await axios.get('/nft-rental/all', {
          params: {
            available_for_rent: 1
          }
        });
        
        nfts.value = response.data;
      } catch (error) {
        console.error('Error fetching available NFTs:', error);
      } finally {
        loading.value = false;
      }
    };
    
    const refreshData = () => {
      fetchAvailableNfts();
    };
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatUserTag = (userTag) => {
      return userTag || 'Unknown';
    };
    
    const getRentDaysOptions = (maxDays) => {
      // Ограничиваем максимальное количество дней для аренды до 7
      const maxAllowedDays = Math.min(maxDays, 7);
      return Array.from({ length: maxAllowedDays }, (_, i) => i + 1);
    };
    
    const getDiscountText = (days) => {
      // Таблица скидок в зависимости от количества дней
      const discounts = {
        1: 0,
        2: 5,
        3: 10,
        4: 15,
        5: 18,
        6: 22,
        7: 25
      };
      
      const discount = discounts[days] || 0;
      return discount > 0 ? `(-${discount}%)` : '';
    };
    
    const calculateEndDate = (days) => {
      const endDate = new Date();
      endDate.setDate(endDate.getDate() + parseInt(days));
      return endDate.toLocaleDateString();
    };
    
    const formatDate = (dateString) => {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    const selectedDaysChanged = (nft) => {
      const days = rentDays[nft.id];
      if (!days) return;
      
      // Получаем информацию о скидках для выбранного количества дней
      const discounts = {
        1: 0,
        2: 5,
        3: 10,
        4: 15,
        5: 18,
        6: 22,
        7: 25
      };
      
      const discountPercent = discounts[days] || 0;
      const discountMultiplier = (100 - discountPercent) / 100;
      const originalDailyPrice = parseFloat(nft.dayPrice);
      const discountedDailyPrice = originalDailyPrice * discountMultiplier;
      const totalPrice = discountedDailyPrice * days;
      const platformFee = totalPrice * 0.05; // 5% комиссия платформы
      
      discountInfo[nft.id] = {
        days,
        discountPercent,
        originalDailyPrice,
        discountedDailyPrice,
        totalPrice,
        platformFee
      };
    };
    
    const isOwnNft = (nft) => {
      console.log('Comparing nft.userTag:', nft.userTag, 'with userTag:', userTag.value);
      if (!nft.userTag || !userTag.value) return false;
      
      // Приводим оба значения к нижнему регистру и удаляем пробелы
      const normalizedNftUserTag = nft.userTag.toLowerCase().trim();
      const normalizedUserTag = userTag.value.toLowerCase().trim();
      
      console.log('Normalized comparison:', normalizedNftUserTag, '===', normalizedUserTag);
      return normalizedNftUserTag === normalizedUserTag;
    };
    
    const rentNft = (nft) => {
      if (!isAuthenticated.value) {
        return;
      }
      
      // Проверяем, не является ли пользователь владельцем NFT
      if (isOwnNft(nft)) {
        toast.type = 'error';
        toast.message = 'You cannot rent your own NFT';
        toast.show = true;
        return;
      }
      
      selectedNft.value = nft;
      currentRentalInfo.value = null;
      showRentModal.value = true;
      
      // Запрашиваем информацию о транзакции для аренды
      fetchRentTransactionInfo(nft);
    };
    
    const fetchRentTransactionInfo = async (nft) => {
      if (!nft || !rentDays[nft.id]) return;
      
      try {
        const response = await axios.post('/nft-rental/rent', {
          rentalId: nft.id,
          days: rentDays[nft.id],
          renterWallet: store.getters.walletAddress
        }, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        // Сохраняем информацию о предстоящей аренде
        currentRentalInfo.value = response.data.rental;
      } catch (error) {
        console.error('Error fetching rent transaction info:', error);
        toast.type = 'error';
        toast.message = `Failed to get rental information: ${error.response?.data?.error || error.message || 'Unknown error'}`;
        toast.show = true;
      }
    };
    
    const closeRentModal = () => {
      if(transactionStatus.value === 'pending') {
        return;
      }

      showRentModal.value = false;
      selectedNft.value = null;
      currentRentalInfo.value = null;
      transactionStatus.value = null;
      transactionSignature.value = null;
      transactionError.value = null;
    };
    
    const retryRental = () => {
      // Reset transaction status and retry
      transactionStatus.value = null;
      transactionSignature.value = null;
      transactionError.value = null;
      // Don't reset selectedNft and currentRentalInfo to allow retry
    };
    
    const confirmRent = async () => {
      if (!selectedNft.value || !rentDays[selectedNft.value.id] || !currentRentalInfo.value) {
        return;
      }
      
      try {
        rentLoading.value = true;
        transactionStatus.value = null;
        transactionSignature.value = null;
        transactionError.value = null;
        
        // Получаем инструкцию для транзакции
        const rentResponse = await axios.post('/nft-rental/rent', {
          rentalId: selectedNft.value.id,
          days: rentDays[selectedNft.value.id],
          renterWallet: store.getters.walletAddress
        }, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        const { transaction: base64Transaction } = rentResponse.data;
        
        // Convert base64 transaction to Transaction object
        const transactionBytes = Uint8Array.from(atob(base64Transaction), c => c.charCodeAt(0));
        const transaction = Transaction.from(transactionBytes);
        
        // Set transaction status to pending
        transactionStatus.value = 'pending';
        
        // Sign and send transaction using Phantom wallet
        try {
          const phantomWallet = window.solana;
          
          if (!phantomWallet) {
            throw new Error('Phantom wallet not found');
          }
          
          const signature = await phantomWallet.signAndSendTransaction(transaction);
          transactionSignature.value = signature.signature;
          
          // Подтверждаем аренду на сервере с подписью транзакции
          const confirmResponse = await axios.post('/nft-rental/confirm', {
            rentalId: selectedNft.value.id,
            signature: signature.signature,
            rentedBy: store.state.userTag,
            days: rentDays[selectedNft.value.id]
          }, {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          });
          
          // Сохраняем количество дней в объекте currentRentalInfo
          if (currentRentalInfo.value) {
            currentRentalInfo.value.days = rentDays[selectedNft.value.id];
          }
          
          // Set transaction status to success
          transactionStatus.value = 'success';
          
          // Добавляем задержку в 2 секунды перед обновлением истории аренды
          setTimeout(async () => {
            // Обновляем историю аренды через Vuex store
             await store.dispatch('fetchRentalHistory');
          }, 2000);
          
          // Reset rental days and refresh available NFTs after showing success
          setTimeout(() => {
            rentDays[selectedNft.value.id] = '';
            fetchAvailableNfts();
          }, 500);
          
        } catch (error) {
          console.error('Transaction error:', error);
          transactionStatus.value = 'error';
          transactionError.value = error.message || 'Failed to sign or confirm transaction';
        }
      } catch (error) {
        console.error('Rent error:', error);
        transactionStatus.value = 'error';
        transactionError.value = error.response?.data?.error || error.message || 'Failed to create rental transaction';
      } finally {
        rentLoading.value = false;
      }
    };
    
    const toggleDropdown = (nftId) => {
      openDropdown.value = openDropdown.value === nftId ? null : nftId;
    };

    const selectDays = (nft, days) => {
      rentDays[nft.id] = days;
      selectedDaysChanged(nft);
      openDropdown.value = null;
    };
    
    onMounted(() => {
      fetchAvailableNfts();
      
      // Устанавливаем значение по умолчанию для всех NFT
      nfts.value.forEach(nft => {
        if (!rentDays[nft.id]) {
          rentDays[nft.id] = 1;
          selectedDaysChanged(nft);
        }
      });

      document.addEventListener('click', (e) => {
        if (openDropdown.value !== null && !e.target.closest('.custom-select')) {
          openDropdown.value = null;
        }
      });
    });
    
    return {
      loading,
      rentLoading,
      nfts,
      filteredNfts,
      searchQuery,
      maxPriceFilter,
      rentDays,
      discountInfo,
      showRentModal,
      selectedNft,
      currentRentalInfo,
      isAuthenticated,
      walletAddress,
      userTag,
      toast,
      refreshData,
      formatPrice,
      formatUserTag,
      getRentDaysOptions,
      getDiscountText,
      calculateEndDate,
      formatDate,
      selectedDaysChanged,
      rentNft,
      closeRentModal,
      confirmRent,
      isOwnNft,
      openDropdown,
      toggleDropdown,
      selectDays,
      retryRental,
      transactionStatus,
      transactionSignature,
      transactionError,
    };
  }
};
</script>

