<template>
  <div class="nft-rental-page">
    <div class="nft-rental-container">
      <h1 class="page-title">NFT Rental Market</h1>
      <p class="subtitle">List your NFTs for rental or rent NFTs from other users</p>

      <!-- Section 1: List NFT for Rent -->
      <section class="rent-listing-section">
        <ListNftForRent 
          :wallet="walletPublicKey" 
          :userTag="userTag"
          @nft-listed="handleNftListed" 
        />
      </section>

      <!-- Section 2: Tabs for Available, My Listed, My Rented -->
      <section class="rental-tabs-section">
        <div class="tab-container">
          <div class="tabs">
            <div class="tabs-navigation">
              <button 
                class="tab-button" 
                :class="{ active: currentTab === 'available' }"
                @click="currentTab = 'available'"
              >
                <i class="fas fa-store tab-icon"></i>
                Available
                <div v-if="currentTab === 'available'" class="active-indicator"></div>
              </button>
              <button 
                class="tab-button"
                :class="{ active: currentTab === 'my-listed' }"
                @click="currentTab = 'my-listed'"
              >
                <i class="fas fa-list tab-icon"></i>
                Listed
                <div v-if="currentTab === 'my-listed'" class="active-indicator"></div>
              </button>
              <button 
                class="tab-button"
                :class="{ active: currentTab === 'my-rented' }"
                @click="currentTab = 'my-rented'"
              >
                <i class="fas fa-box-open tab-icon"></i>
                Rented
                <div v-if="currentTab === 'my-rented'" class="active-indicator"></div>
              </button>
            </div>
          </div>

          <div class="tab-content">
            <!-- Available NFTs for Rent -->
            <AvailableNfts 
              v-if="currentTab === 'available'" 
              :key="'available'" 
              @rental-success="handleRentalSuccess"
            />

            <!-- My NFTs for Rent -->
            <MyListedNfts 
              v-if="currentTab === 'my-listed'" 
              :key="'my-listed'"
              :wallet="walletPublicKey"
              :userTag="userTag"
            />

            <!-- My Rented NFTs -->
            <MyRentedNfts 
              v-if="currentTab === 'my-rented'" 
              :key="'my-rented'"
              :wallet="walletPublicKey"
              :userTag="userTag"
            />
          </div>
        </div>
      </section>

      <!-- Section 3: Rental History -->
      <section class="rental-history-section">
        <RentalHistory />
      </section>
    </div>

    <!-- Модальное окно для отображения статуса транзакции -->
    <div v-if="transactionStatus" class="transaction-modal">
      <div class="modal-content">
        <h3>{{ transactionStatus.title }}</h3>
        <p>{{ transactionStatus.message }}</p>
        <div v-if="transactionStatus.isLoading" class="loading-spinner"></div>
        <div v-if="transactionStatus.error" class="error-message">
          {{ transactionStatus.error }}
        </div>
        <button 
          v-if="!transactionStatus.isLoading" 
          @click="transactionStatus = null"
          class="close-button"
        >
          Close
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue';
import { useRouter } from 'vue-router';
import store from '@/vuex'

// Обычный импорт компонентов
import AvailableNfts from '@/components/pages/nft-rental/AvailableNfts.vue';
import MyListedNfts from '@/components/pages/nft-rental/MyListedNfts.vue';
import MyRentedNfts from '@/components/pages/nft-rental/MyRentedNfts.vue';
import RentalHistory from '@/components/pages/nft-rental/RentalHistory.vue';
import ListNftForRent from '@/components/pages/nft-rental/ListNftForRent.vue';

export default {
  name: 'NftRentalPage',
  components: {
    AvailableNfts,
    MyListedNfts,
    MyRentedNfts,
    RentalHistory,
    ListNftForRent
  },
  setup() {
    const router = useRouter();
    const currentTab = ref('available');
    
    const tabs = computed(() => {
      const allTabs = [
        { id: 'available', label: 'Available NFTs' },
        { id: 'my-listed', label: 'My Listed NFTs' },
        { id: 'my-rented', label: 'My Rented NFTs' }
      ];
      
      return allTabs;
    });
    
    // If user tries to access a protected tab without authentication, redirect to available
    onMounted(() => {
      if (!store.getters.isAuthenticated && currentTab.value !== 'available') {
        currentTab.value = 'available';
      }

      // Добавляем обработчик события переключения таба
      window.addEventListener('switch-tab', (event) => {
        currentTab.value = event.detail;
      });
    });
    
    // Добавляем очистку обработчика при размонтировании
    onUnmounted(() => {
      window.removeEventListener('switch-tab', (event) => {
        currentTab.value = event.detail;
      });
    });
    
    const handleNftListed = () => {
      // Switch to My Listed NFTs tab after successful listing
      currentTab.value = 'my-listed';
    };
    
    // Добавляем вычисляемые свойства для безопасного доступа к Vuex
    const userTag = computed(() => {
      return store.getters.userTag || '';
    });
    
    const walletPublicKey = computed(() => {
      return store.getters.walletAddress || '';
    });

    const transactionStatus = ref(null);

    const handleRentalSuccess = (rental) => {
      transactionStatus.value = {
        title: 'Аренда успешна',
        message: `NFT успешно арендован до ${new Date(rental.rentalEndTime).toLocaleDateString()}`,
        isLoading: false,
        error: null
      };
    };

    return {
      currentTab,
      tabs,
      handleNftListed,
      userTag,
      walletPublicKey,
      transactionStatus,
      handleRentalSuccess
    };
  }
};
</script>

