<template>
  <div class="my-rented-nfts">
    <h2 class="section-title">My Rented NFTs</h2>
    
    <div v-if="!isAuthenticated" class="auth-message">
      <p>Please connect your wallet to view your rented NFTs.</p>
    </div>
    
    <div v-else-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading your rented NFTs...</p>
    </div>
    
    <div v-else-if="rentedNfts.length === 0" class="empty-state">
      <p>You haven't rented any NFTs yet.</p>
      <button @click="goToAvailableNfts" class="primary-button">Browse Available NFTs</button>
    </div>
    
    <div v-else class="nft-grid">
      <div v-for="nft in rentedNfts" :key="nft.id" class="nft-card">
        <div class="nft-image-container">
          <img :src="nft.imageUrl || '/images/default-nft.png'" :alt="nft.name" class="nft-image" />
          <div class="time-remaining-badge" :class="getTimeRemainingClass(nft)">
            {{ getTimeRemaining(nft) }}
          </div>
        </div>
        <div class="nft-info">
          <h3 class="nft-name">{{ nft.name || 'Unnamed NFT' }}</h3>
          
          <div class="nft-details">
            <div class="detail-item">
              <span class="detail-label">Owner:</span>
              <router-link :to="`/player/${nft.userTag}`" class="detail-value">{{ nft.userTag }}</router-link>
            </div>
            
            <div class="detail-item">
              <span class="detail-label">Daily Price:</span>
              <span class="detail-value">{{ formatPrice(nft.dayPrice) }} SOL</span>
            </div>
            
            <div class="detail-item">
              <span class="detail-label">Rental End:</span>
              <span class="detail-value">{{ formatDate(nft.rentalEndTime) }}</span>
            </div>
          </div>
          
          <div class="transaction-info">
            <button 
              v-if="nft.transactionSignature" 
              @click="viewTransaction(nft.transactionSignature)"
              class="view-tx-button"
            >
              View Transaction
            </button>
          </div>
          
          <div class="nft-actions">
            <router-link 
              :to="`/nft/${nft.mint}`" 
              class="view-details-button"
            >
              View NFT Details
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import { useRouter } from 'vue-router';

export default {
  name: 'MyRentedNfts',
  props: {
    wallet: {
      type: String,
      default: ''
    },
    userTag: {
      type: String,
      default: ''
    }
  },
  setup(props) {
    const store = useStore();
    const router = useRouter();
    const loading = ref(true);
    const rentedNfts = ref([]);
    
    const isAuthenticated = computed(() => !!props.wallet && !!props.userTag);
    
    const fetchRentedNfts = async () => {
      if (!isAuthenticated.value) {
        rentedNfts.value = [];
        return;
      }
      
      try {
        loading.value = true;
        const response = await axios.get(`/nft-rental/rented-by/${props.userTag}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        rentedNfts.value = response.data;
      } catch (error) {
        console.error('Error fetching rented NFTs:', error);
        // store.dispatch('notify', {
        //   type: 'error',
        //   message: 'Failed to load your rented NFTs'
        // });
      } finally {
        loading.value = false;
      }
    };
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatDate = (dateString) => {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    const getTimeRemaining = (nft) => {
      const now = new Date();
      const endTime = new Date(nft.rentalEndTime);
      const diffTime = endTime - now;
      
      if (diffTime <= 0) {
        return 'Expired';
      }
      
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
      const diffHours = Math.floor((diffTime % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      
      if (diffDays > 0) {
        return `${diffDays}d ${diffHours}h left`;
      } else {
        const diffMinutes = Math.floor((diffTime % (1000 * 60 * 60)) / (1000 * 60));
        return `${diffHours}h ${diffMinutes}m left`;
      }
    };
    
    const getTimeRemainingClass = (nft) => {
      const now = new Date();
      const endTime = new Date(nft.rentalEndTime);
      const diffTime = endTime - now;
      
      if (diffTime <= 0) {
        return 'expired';
      }
      
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays <= 1) {
        return 'expiring-soon';
      } else {
        return 'active';
      }
    };
    
    const isRentalExpiring = (nft) => {
      const now = new Date();
      const endTime = new Date(nft.rentalEndTime);
      const diffTime = endTime - now;
      
      if (diffTime <= 0) {
        return false; // Already expired
      }
      
      const diffDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));
      
      return diffDays <= 2; // Expiring soon if 2 days or less remaining
    };
    
    const viewTransaction = (signature) => {
      const explorerUrl = `https://explorer.solana.com/tx/${signature}`;
      window.open(explorerUrl, '_blank');
    };
    
    const goToAvailableNfts = () => {
      // Используем событие для переключения таба
      window.dispatchEvent(new CustomEvent('switch-tab', { detail: 'available' }));
    };
    
    onMounted(() => {
      fetchRentedNfts();
      
      // Set up auto-refresh every minute to update time remaining
      const refreshInterval = setInterval(() => {
        if (rentedNfts.value.length > 0) {
          // Force UI update by creating a new array
          rentedNfts.value = [...rentedNfts.value];
        }
      }, 60000);
      
      return () => {
        clearInterval(refreshInterval);
      };
    });
    
    return {
      loading,
      rentedNfts,
      isAuthenticated,
      formatPrice,
      formatDate,
      getTimeRemaining,
      getTimeRemainingClass,
      isRentalExpiring,
      viewTransaction,
      goToAvailableNfts
    };
  }
};
</script>

