<template>
  <div class="my-listed-nfts">
    <h2 class="section-title">My Listed NFTs</h2>
    
    <div v-if="!isAuthenticated" class="auth-message">
      <p>Please connect your wallet to view your listed NFTs.</p>
    </div>
    
    <div v-else-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading your listed NFTs...</p>
    </div>
    
    <div v-else-if="listedNfts.length === 0" class="empty-state">
      <p>You haven't listed any NFTs for rent yet.<br>List an NFT for Rent</p>
    </div>
    
    <div v-else class="nft-grid">
      <div v-for="nft in listedNfts" :key="nft.id" class="nft-card">
        <div class="nft-image-container">
          <img :src="nft.imageUrl || '/images/default-nft.png'" :alt="nft.name" class="nft-image" />
        </div>
        <div class="nft-info">
          <h3 class="nft-name"><router-link :to="`/nft/${nft.nftMint}`">{{ nft.name || 'Unnamed NFT' }}</router-link></h3>
          
          <div class="nft-price">
            <span class="price-label">Daily Price:</span>
            <span class="price-amount">{{ formatPrice(nft.dayPrice) }} SOL</span>
          </div>
          
          <div class="nft-details">
            <div class="detail-item">
              <span class="detail-label">Max Rental Period:</span>
              <span class="detail-value">{{ nft.maxDays }} days</span>
            </div>
            
            <div class="detail-item">
              <span class="detail-label">Status:</span>
              <span :class="['status-badge', getRentalStatusClass(nft)]">
                {{ getRentalStatus(nft) }}
              </span>
            </div>
            
            <div v-if="nft.rentedBy" class="detail-item">
              <span class="detail-label">Rented By:</span>
              <router-link :to="`/player/${nft.rentedBy}`" class="detail-value">{{ nft.rentedBy }}</router-link>
            </div>
            
            <div v-if="nft.rentalEndTime" class="detail-item">
              <span class="detail-label">Rental Ends:</span>
              <span class="detail-value">{{ formatDate(nft.rentalEndTime) }}</span>
            </div>
          </div>
          
          <div class="nft-actions">
            <button 
              v-if="nft.availableForRent"
              @click="openAvailabilityModal(nft.id, false)"
              class="close-rental-button"
            >
              Close Rental
            </button>
            <button 
              v-else
              @click="openAvailabilityModal(nft.id, true)"
              class="open-rental-button"
            >
              Open Rental
            </button>
            <button 
              @click="openUpdateParametersModal(nft)"
              class="update-parameters-button"
            >
              Update Rental
            </button>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Availability Confirmation Modal -->
    <div v-if="showAvailabilityModal" class="modal-overlay" @click="closeAvailabilityModal">
      <div class="modal-content" @click.stop>
        <h3>{{ availabilityModalTitle }}</h3>
        <p>{{ availabilityModalMessage }}</p>
        <div class="modal-actions">
          <button @click="closeAvailabilityModal" class="secondary-button">Cancel</button>
          <button 
            @click="confirmAvailabilityChange" 
            :disabled="availabilityLoading"
            :class="['primary-button', availabilityModalType]"
          >
            <span v-if="availabilityLoading" class="loading-spinner small"></span>
            <span v-else>{{ availabilityModalAction }}</span>
          </button>
        </div>
      </div>
    </div>

    <!-- Update Parameters Modal -->
    <div v-if="showUpdateParametersModal" class="modal-overlay" @click="closeUpdateParametersModal">
      <div class="modal-content" @click.stop>
        <h3>Update Rental Parameters</h3>
        <div class="form-group">
          <label for="dayPrice">Daily Price (SOL)</label>
          <input 
            type="number" 
            id="dayPrice" 
            v-model="updateParameters.dayPrice" 
            min="0.001" 
            step="0.001"
            class="form-input"
          />
        </div>
        <div class="form-group">
          <label for="maxDays">Maximum Rental Period (days)</label>
          <input 
            type="number" 
            id="maxDays" 
            v-model="updateParameters.maxDays" 
            min="1" 
            step="1"
            class="form-input"
          />
        </div>
        <div class="modal-actions">
          <button @click="closeUpdateParametersModal" class="secondary-button">Cancel</button>
          <button 
            @click="confirmUpdateParameters" 
            :disabled="updateParametersLoading"
            class="primary-button update"
          >
            <span v-if="updateParametersLoading" class="loading-spinner small"></span>
            <span v-else>Update Parameters</span>
          </button>
        </div>
      </div>
    </div>
  </div>
  
  <AppToast 
    :show="toast.show" 
    :message="toast.message" 
    :type="toast.type" 
    @close="toast.show = false" 
    :duration="toast.duration"
  />
</template>

<script>
import { ref, computed, onMounted, reactive } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import AppToast from '@/components/Toast.vue';

export default {
  name: 'MyListedNfts',
  components: {
    AppToast
  },
  props: {
    wallet: {
      type: String,
      default: ''
    },
    userTag: {
      type: String,
      default: ''
    }
  },
  setup(props, { emit }) {
    const store = useStore();
    const loading = ref(true);
    const listedNfts = ref([]);
    const cancelLoading = reactive({});
    const showCancelModal = ref(false);
    const selectedNft = ref(null);
    const confirmCancelLoading = ref(false);
    const showAvailabilityModal = ref(false);
    const availabilityModalTitle = ref('');
    const availabilityModalMessage = ref('');
    const availabilityModalAction = ref('');
    const availabilityModalType = ref('');
    const availabilityLoading = ref(false);
    const selectedNftId = ref(null);
    const selectedAvailability = ref(null);
    
    // Toast notification state
    const toast = reactive({
      show: false,
      message: '',
      type: 'info', // 'success', 'error', 'info', 'warning'
      duration: 5000
    });
    const showUpdateParametersModal = ref(false);
    const updateParameters = ref({
      dayPrice: '',
      maxDays: ''
    });
    const updateParametersLoading = ref(false);
    const selectedNftForUpdate = ref(null);
    
    const isAuthenticated = computed(() => !!props.wallet && !!props.userTag);
    
    const fetchListedNfts = async () => {
      if (!isAuthenticated.value) {
        listedNfts.value = [];
        return;
      }
      
      try {
        loading.value = true;
        const response = await axios.get(`/nft-rental/user/${props.userTag}`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        listedNfts.value = response.data;
      } catch (error) {
        console.error('Error fetching listed NFTs:', error);
        toast.type = 'error';
        toast.message = 'Failed to load your listed NFTs';
        toast.show = true;
      } finally {
        loading.value = false;
      }
    };
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatDate = (dateString) => {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    
    const getRentalStatus = (nft) => {
      if (!nft.availableForRent && nft.rentedBy) {
        return 'Rented';
      } else if (!nft.availableForRent) {
        return 'Paused';
      } else if (nft.availableForRent) {
        return 'Available';
      } else {
        return 'Unavailable';
      }
    };
    
    const getRentalStatusClass = (nft) => {
      if (!nft.availableForRent && nft.rentedBy) {
        return 'status-rented';
      } else if (!nft.availableForRent) {
        return 'status-paused';
      } else if (nft.availableForRent) {
        return 'status-available';
      } else {
        return 'status-unavailable';
      }
    };
    
    const cancelListing = (nft) => {
      selectedNft.value = nft;
      showCancelModal.value = true;
    };
    
    const closeCancelModal = () => {
      showCancelModal.value = false;
      selectedNft.value = null;
    };
    
    const confirmCancel = async () => {
      if (!selectedNft.value) return;
      
      try {
        confirmCancelLoading.value = true;
        cancelLoading[selectedNft.value.id] = true;
        
        await axios.post('/nft-rental/cancel', {
          rentalId: selectedNft.value.id,
          userTag: props.userTag
        }, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        // Update the UI
        listedNfts.value = listedNfts.value.filter(nft => nft.id !== selectedNft.value.id);
        
        closeCancelModal();
      } catch (error) {
        console.error('Error cancelling rental listing:', error);
        
        toast.type = 'error';
        toast.message = `Failed to cancel listing: ${error.response?.data?.error || error.message || 'Unknown error'}`;
        toast.show = true;
      } finally {
        confirmCancelLoading.value = false;
        if (selectedNft.value) {
          cancelLoading[selectedNft.value.id] = false;
        }
      }
    };
    
    const updateAvailability = async (nftId, available) => {
      try {
        const response = await axios.put(
          `/nft-rental/listings/${nftId}/availability`,
          { available_for_rent: available ? 1 : 0 },
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        );

        if (response.data.success) {
          // Обновляем состояние конкретного NFT в списке
          const nftIndex = listedNfts.value.findIndex(nft => nft.id === nftId);
          if (nftIndex !== -1) {
            listedNfts.value[nftIndex].availableForRent = available ? 1 : 0;
          }
          
        }
      } catch (error) {
        console.error('Error updating NFT availability:', error);
        toast.type = 'error';
        toast.message = error.response?.data?.error || 'Failed to update NFT availability';
        toast.show = true;
      }
    };
    
    const openAvailabilityModal = (nftId, available) => {
      selectedNftId.value = nftId;
      selectedAvailability.value = available;
      
      if (available) {
        availabilityModalTitle.value = 'Open NFT for Rental';
        availabilityModalMessage.value = 'By opening this NFT for rental, it will become available for anyone to rent. Are you sure you want to proceed?';
        availabilityModalAction.value = 'Open Rental';
        availabilityModalType.value = 'open';
      } else {
        availabilityModalTitle.value = 'Close NFT Rental';
        availabilityModalMessage.value = 'By closing this NFT for rental, it will no longer be available for new rentals. If the NFT is currently rented, the current rental will continue until its end date. Are you sure you want to proceed?';
        availabilityModalAction.value = 'Close Rental';
        availabilityModalType.value = 'close';
      }
      
      showAvailabilityModal.value = true;
    };
    
    const closeAvailabilityModal = () => {
      showAvailabilityModal.value = false;
      selectedNftId.value = null;
      selectedAvailability.value = null;
      availabilityLoading.value = false;
    };
    
    const confirmAvailabilityChange = async () => {
      if (!selectedNftId.value || selectedAvailability.value === null) return;
      
      try {
        availabilityLoading.value = true;
        await updateAvailability(selectedNftId.value, selectedAvailability.value);
        closeAvailabilityModal();
      } catch (error) {
        console.error('Error confirming availability change:', error);
      }
    };
    
    const openUpdateParametersModal = (nft) => {
      selectedNftForUpdate.value = nft;
      updateParameters.value = {
        dayPrice: nft.dayPrice,
        maxDays: nft.maxDays
      };
      showUpdateParametersModal.value = true;
    };
    
    const closeUpdateParametersModal = () => {
      showUpdateParametersModal.value = false;
      selectedNftForUpdate.value = null;
      updateParameters.value = {
        dayPrice: '',
        maxDays: ''
      };
      updateParametersLoading.value = false;
    };
    
    const confirmUpdateParameters = async () => {
      if (!selectedNftForUpdate.value) return;
      
      try {
        updateParametersLoading.value = true;
        const response = await axios.put(
          `/nft-rental/listings/${selectedNftForUpdate.value.id}/parameters`,
          updateParameters.value,
          {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
            }
          }
        );

        if (response.data.success) {
          // Обновляем состояние конкретного NFT в списке
          const nftIndex = listedNfts.value.findIndex(nft => nft.id === selectedNftForUpdate.value.id);
          if (nftIndex !== -1) {
            listedNfts.value[nftIndex].dayPrice = updateParameters.value.dayPrice;
            listedNfts.value[nftIndex].maxDays = updateParameters.value.maxDays;
          }
          closeUpdateParametersModal();
        }
      } catch (error) {
        console.error('Error updating rental parameters:', error);
        toast.type = 'error';
        toast.message = error.response?.data?.error || 'Failed to update rental parameters';
        toast.show = true;
      } finally {
        updateParametersLoading.value = false;
      }
    };
    
    onMounted(() => {
      fetchListedNfts();
    });
    
    return {
      loading,
      listedNfts,
      cancelLoading,
      showCancelModal,
      confirmCancelLoading,
      isAuthenticated,
      formatPrice,
      formatDate,
      getRentalStatus,
      getRentalStatusClass,
      cancelListing,
      closeCancelModal,
      confirmCancel,
      updateAvailability,
      showAvailabilityModal,
      availabilityModalTitle,
      availabilityModalMessage,
      availabilityModalAction,
      availabilityModalType,
      availabilityLoading,
      selectedNftId,
      selectedAvailability,
      openAvailabilityModal,
      closeAvailabilityModal,
      confirmAvailabilityChange,
      showUpdateParametersModal,
      updateParameters,
      toast,
      updateParametersLoading,
      selectedNftForUpdate,
      openUpdateParametersModal,
      closeUpdateParametersModal,
      confirmUpdateParameters
    };
  }
};
</script>

