<template>
  <div class="list-nft-for-rent">
    <h2 class="section-title">List Your NFT for Rent</h2>
    
    <div v-if="!isAuthenticated" class="auth-message">
      <p>Please connect your wallet to list NFTs for rent.</p>
    </div>
    
    <div v-else>
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>Loading your NFTs...</p>
      </div>
      
      <div v-else-if="userNfts.length === 0" class="empty-state">
        <p>You don't have any NFTs to list for rent.</p>
        <div class="empty-actions">
          <router-link to="/nft-mint" class="mint-btn">Mint new NFT</router-link>
        </div>
      </div>
      
      <div v-else>
        <div class="nft-selection">
          <label class="section-label">1. Choose an NFT to list for rent</label>
          <div class="nft-grid">
            <div 
              v-for="(nft, index) in userNfts" 
              :key="index"
              :class="['nft-card', { selected: selectedNft && selectedNft.createdAt === nft.createdAt }]"
              @click="selectNft(nft)"
            >
              <div class="nft-image-container">
                <img 
                  :src="nft.imageUri || '/images/default-nft.png'" 
                  :alt="nft.name" 
                  class="nft-image" 
                  loading="lazy"
                />
              </div>
              <div class="nft-info">
                <h3 class="nft-name">{{ nft.name || 'Unnamed NFT' }}</h3>
              </div>
            </div>
          </div>
        </div>
        
        <div v-if="selectedNft" class="rental-form">
          <div class="section-divider"></div>
          
          <label class="section-label">2. Set rental details</label>
          <div class="form-group">
            <label for="day-price">Daily Price (SOL)</label>
            <input 
              id="day-price"
              v-model="dayPrice"
              type="number"
              min="0.001"
              step="0.001"
              placeholder="Daily rental price in SOL"
              class="input-field"
            />
            <div v-if="priceError" class="error-message">{{ priceError }}</div>
          </div>
          
          <div class="form-group">
            <label for="max-days">Maximum Rental Period (days)</label>
            <input 
              id="max-days"
              v-model="maxDays"
              type="number"
              min="1"
              max="7"
              placeholder="Maximum rental period in days"
              class="input-field"
            />
            <small class="helper-text">Maximum allowed rental period is 7 days</small>
            <div v-if="daysError" class="error-message">{{ daysError }}</div>
          </div>
          
          <div v-if="dayPrice && maxDays && !priceError && !daysError" class="discount-table">
            <h4 class="discount-table-title">Rental Discount Rates</h4>
            <p class="discount-table-description">Longer rental periods offer discounts to encourage extended use:</p>
            
            <div class="discount-table-container">
              <table>
                <thead>
                  <tr>
                    <th>Days</th>
                    <th>Discount</th>
                    <th>Daily Price</th>
                    <th>Total</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in discountTable" :key="index" :class="{ 'highlight': item.days === parseInt(maxDays) }">
                    <td>{{ item.days }}</td>
                    <td>{{ item.discountPercent }}%</td>
                    <td>{{ formatPrice(item.discountedDailyPrice) }} SOL</td>
                    <td>{{ formatPrice(item.totalPrice) }} SOL</td>
                  </tr>
                </tbody>
              </table>
            </div>
            
            <p class="platform-fee-note">Note: A 5% platform fee will be charged to the tenant at time of rental.</p>
          </div>
          
          <!-- Отображение ошибок с бэкенда -->
          <div v-if="backendError" class="backend-error-message error-message">
            <p>Error: {{ backendError }}</p>
          </div>
          
          <div class="buttons-container">
            <button @click="cancelSelection" class="cancel-button">
              Cancel
            </button>
            <button 
              @click="listNftForRent" 
              class="list-button"
              :disabled="listLoading || !isFormValid"
            >
              <span v-if="listLoading" class="loading-spinner small"></span>
              <span v-else>List NFT for Rent</span>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
  <AppToast 
    :show="toast.show" 
    :message="toast.message" 
    :type="toast.type" 
    @close="toast.show = false" 
    :duration="toast.duration"
  />
</template>

<script>
import { ref, computed, onMounted, watch, reactive } from 'vue';
import { useStore } from 'vuex';
import axios from 'axios';
import { PublicKey, Transaction } from '@solana/web3.js';
import AppToast from '@/components/Toast.vue';

export default {
  name: 'ListNftForRent',
  components: {
    AppToast
  },
  props: {
    wallet: {
      type: String,
      default: ''
    },
    userTag: {
      type: String,
      default: ''
    }
  },
  emits: ['nft-listed'],
  setup(props, { emit }) {
    const store = useStore();
    const loading = ref(true);
    const listLoading = ref(false);
    const userNfts = ref([]);
    const selectedNft = ref(null);
    const dayPrice = ref('0.1');
    const maxDays = ref(7);
    
    // Toast notification state
    const toast = reactive({
      show: false,
      message: '',
      type: 'info', // 'success', 'error', 'info', 'warning'
      duration: 5000
    });
    const priceError = ref('');
    const daysError = ref('');
    const backendError = ref('');
    
    const isAuthenticated = computed(() => !!props.wallet && !!props.userTag);
    
    const isFormValid = computed(() => {
      return !priceError.value && !daysError.value && 
             dayPrice.value && parseFloat(dayPrice.value) > 0 &&
             maxDays.value && parseInt(maxDays.value) > 0;
    });
    
    const validatePrice = () => {
      priceError.value = '';
      const price = parseFloat(dayPrice.value);
      
      if (isNaN(price)) {
        priceError.value = 'Please enter a valid price';
        return false;
      }
      
      if (price <= 0) {
        priceError.value = 'Price must be greater than 0';
        return false;
      }
      
      if (price < 0.001) {
        priceError.value = 'Minimum price is 0.001 SOL';
        return false;
      }
      
      return true;
    };
    
    const validateDays = () => {
      daysError.value = '';
      const days = parseInt(maxDays.value);
      
      if (isNaN(days)) {
        daysError.value = 'Please enter a valid number of days';
        return false;
      }
      
      if (days <= 0) {
        daysError.value = 'Rental period must be at least 1 day';
        return false;
      }
      
      if (days > 7) {
        daysError.value = 'Maximum rental period is 7 days';
        return false;
      }
      
      return true;
    };
    
    // Расчет таблицы скидок
    const discountTable = computed(() => {
      if (!dayPrice.value || parseFloat(dayPrice.value) <= 0) return [];
      
      // Таблица скидок в зависимости от количества дней
      const discounts = {
        1: 0,
        2: 5,
        3: 10,
        4: 15,
        5: 18,
        6: 22,
        7: 25
      };
      
      const result = [];
      const basePrice = parseFloat(dayPrice.value);
      const calculatedMaxDays = parseInt(maxDays.value) || 7;
      const maxAllowedDays = Math.min(calculatedMaxDays, 7);
      
      for (let days = 1; days <= maxAllowedDays; days++) {
        const discountPercent = discounts[days] || 0;
        const discountMultiplier = (100 - discountPercent) / 100;
        const discountedDailyPrice = basePrice * discountMultiplier;
        const totalPrice = discountedDailyPrice * days;
        
        result.push({
          days,
          discountPercent,
          discountedDailyPrice,
          totalPrice
        });
      }
      
      return result;
    });
    
    const fetchUserNfts = async () => {
      if (!isAuthenticated.value) {
        userNfts.value = [];
        loading.value = false; // Убедимся, что загрузка отключена
        return;
      }
      
      try {
        loading.value = true;
        console.log('Fetching user NFTs...');
        
        const response = await axios.get(`/nft-rental/nfts`, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        console.log('NFT response:', response.data);
        
        if (response.data) {
          if (response.data.success) {
            userNfts.value = response.data.nfts || [];
            console.log(`Loaded ${userNfts.value.length} NFTs`);
          } else {
            // Если success: false, показываем ошибку
            console.error('Error in NFT response:', response.data.error || 'Unknown error');
            toast.type = 'error';
            toast.message = response.data.error || 'Failed to load your NFTs';
            toast.show = true;
            userNfts.value = []; // Сбрасываем список NFT
          }
        } else {
          // Если нет данных в ответе
          console.error('No data in NFT response');
          userNfts.value = [];
        }
      } catch (error) {
        console.error('Error fetching user NFTs:', error);
        toast.type = 'error';
        toast.message = error.response?.data?.error || 'Failed to load your NFTs';
        toast.show = true;
        userNfts.value = []; // Сбрасываем список NFT
      } finally {
        loading.value = false; // Всегда отключаем индикатор загрузки
      }
    };
    
    const selectNft = (nft) => {
      selectedNft.value = nft;
      validatePrice();
      validateDays();
    };
    
    const cancelSelection = () => {
      selectedNft.value = null;
      dayPrice.value = '0.1';
      maxDays.value = 7;
      priceError.value = '';
      daysError.value = '';
      backendError.value = '';
    };
    
    const listNftForRent = async () => {
      if (!selectedNft.value || !isAuthenticated.value) {
        return;
      }
      
      // Validate inputs
      const isPriceValid = validatePrice();
      const isDaysValid = validateDays();
      
      if (!isPriceValid || !isDaysValid) {
        return;
      }
      
      try {
        // Очищаем предыдущие ошибки
        backendError.value = '';
        listLoading.value = true;
        
        // Create rental listing with necessary data
        const listingData = {
          userTag: props.userTag,
          nftMint: selectedNft.value.assetPublicKey,
          dayPrice: parseFloat(dayPrice.value),
          maxDays: parseInt(maxDays.value),
          wallet: props.wallet
        };
        
        // Создаем листинг на сервере
        const response = await axios.post('/nft-rental/create', listingData, {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('auth_token')}`
          }
        });
        
        // Проверяем, что сервер вернул информацию о скидках для отображения
        if (response.data && response.data.discounts) {
          console.log('Discounts from server:', response.data.discounts);
        }
        
        // Show success notification
        toast.type = 'success';
        toast.message = 'Your NFT has been listed for rent successfully!';
        toast.show = true;
        
        // Reset form
        cancelSelection();
        
        // Обновляем список NFT после успешного листинга
        await fetchUserNfts();
        
        // Emit event to parent component
        emit('nft-listed');
      } catch (error) {
        console.error('Error listing NFT for rent:', error.response.data.error);


        
        // Сохраняем ошибку с бэкенда для отображения
        backendError.value = error.response.data.error || 'Failed to list NFT';
        
        toast.type = 'error';
        toast.message = `Failed to list NFT: ${backendError.value}`;
        toast.show = true;
      } finally {
        listLoading.value = false;
      }
    };
    
    onMounted(() => {
      fetchUserNfts();
    });
    
    // Функция для форматирования цены (фиксированное количество знаков после запятой)
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    return {
      loading,
      listLoading,
      userNfts,
      selectedNft,
      dayPrice,
      maxDays,
      priceError,
      daysError,
      discountTable,
      isAuthenticated,
      isFormValid,
      selectNft,
      cancelSelection,
      listNftForRent,
      formatPrice,
      backendError,
      toast
    };
  }
};
</script>

