<template>
  <div class="rental-history">
    <h2 class="section-title">Rental History</h2>
    <p class="section-subtitle">Last 20 rented NFTs</p>
    
    <div v-if="loading" class="loading-container">
      <div class="loading-spinner"></div>
      <p>Loading rental history...</p>
    </div>
    
    <div v-else-if="nftRentalHistory.length === 0" class="empty-state">
      <p>No rental history available yet.</p>
    </div>
    
    <div v-else class="rental-history-grid">
      <div v-for="rental in nftRentalHistory" :key="rental.id" class="rental-card">
        <div class="rental-card-header">
          <div class="rental-price">
            <i class="fas fa-coins"></i>
            {{ formatPrice(rental.price) }} SOL
          </div>
          <button 
            v-if="rental.transaction" 
            @click="viewTransaction(rental.transaction)"
            class="view-tx-button"
          >
            <i class="fas fa-receipt"></i> TX
          </button>
        </div>
        
        <div class="rental-card-body">
          <div class="rental-detail-row">
            <span class="detail-label"><i class="fas fa-image"></i> NFT:</span>
            <router-link :to="`/nft/${rental.mint}`" class="nft-link">
              {{ shortenAddress(rental.mint) }}
            </router-link>
          </div>
          
          <div class="rental-detail-row">
            <span class="detail-label"><i class="fas fa-user"></i> Rented By:</span>
            <router-link :to="`/player/${rental.rentedBy}`" class="user-link">
              {{ rental.rentedBy }}
            </router-link>
          </div>
          
          <div class="rental-detail-row">
            <span class="detail-label"><i class="fas fa-calendar-day"></i> {{ formatDate(rental.startDate) }} for {{ rental.days }} day(s)</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, computed } from 'vue';
import { useStore } from 'vuex';

export default {
  name: 'RentalHistory',
  setup() {
    const store = useStore();
    const loading = ref(true);

    const nftRentalHistory = computed(() => store.getters.nftRentalHistory);
    
    const fetchRentalHistory = async () => {
      try {
        loading.value = true;
        // Используем store для получения истории аренды
        await store.dispatch('fetchRentalHistory');
      } catch (error) {
        console.error('Error fetching rental history:', error);
      } finally {
        loading.value = false;
      }
    };
    
    // Функция для сокращения адреса кошелька
    const shortenAddress = (address) => {
      if (!address) return '-';
      return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
    };
    
    const formatPrice = (price) => {
      return parseFloat(price).toFixed(3);
    };
    
    const formatDate = (dateString) => {
      if (!dateString) return '';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    };
    

    
    const viewTransaction = (signature) => {
      const explorerUrl = `https://explorer.solana.com/tx/${signature}`;
      window.open(explorerUrl, '_blank');
    };
    
    onMounted(() => {
      fetchRentalHistory();
    });
    
    return {
      loading,
      formatPrice,
      formatDate,
      shortenAddress,
      viewTransaction,
      nftRentalHistory
    };
  }
};
</script>

